<template>
    <div>
        <label class="b-24-black" v-html="t('activity.title')"></label>
        <br><br>
        <div>
            <p class="r-16-gray px-4 pt-2 pb-1 text-center" v-html="t('activity.info')">
            </p>
        </div>
    </div>
    <div class="text-start px-4 pt-3">
        <p class="b-16-gray" v-html="t('activity.temporalidad')"></p>
        <select class="form-control w-100 select-ap" v-html="t('activity.ultsem')">
           
        </select>
        <div class="numeralia d-flex py-3">
            <div class="num-circle">
                <div class="circle-black b-48-gray p-3">
                    <label class="m-auto">17</label>
                </div>
            </div>
            <div class="data-rec b-20-gray w-100">
                <label class="m-auto px-3 b-20-gray" v-html="t('activity.audio')"></label>
            </div>
        </div>
        <div class="numeralia d-flex py-3">
            <div class="num-circle">
                <div class="circle-black b-32-gray p-3">
                    <label class="m-auto">27m</label>
                </div>
            </div>
            <div class="data-rec b-20-gray w-100">
                <label class="m-auto px-3 b-20-gray" v-html="t('activity.horas')"></label>
            </div>
        </div>
    </div>
    <!-- Gráfica de barras -->
    <div class="py-3 px-4">
        <p class="b-16-gray pt-2 pb-1 text-start"  v-html="t('activity.dias')">
        
        </p>
        <div>
            <Bar style="overflow-y: visible;" :data="barData" :options="barOptions" />
        </div>
    </div>

    <div class="py-3 px-4">
        <p class="b-16-gray pt-2 pb-1 text-start"  v-html="t('activity.momentos')">
           
        </p>
        <div style="display: flex; justify-content: center;">
            <table border="1" style="border-collapse: collapse; width: 100%;">
                <tbody>
                    <!-- Generar filas desde la más alta hacia la más baja -->
                    <tr v-for="rowColor in maxRowsColor" :key="rowColor">
                        <td v-for="col in columnsColor" :key="col"
                            :style="{ backgroundColor: getValueAt(col, maxRowsColor - rowColor) ? obtenerColorAleatorio() : '#fff' }">
                            {{ getValueAt(col, maxRowsColor - rowColor) ?? '' }}
                        </td>
                    </tr>

                    <!-- Fila inferior con nombres de columnas -->
                    <tr>
                        <td v-for="col in columnsColor" :key="col" style="font-weight: bold; text-align: center;">
                            {{ col }}
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div class="d-flex justify-content-start pt-4">
            <div class="mini-square" style="background-color: #efefef;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.registro')">
             
            </div>
        </div>
        <div class="d-flex justify-content-start pt-4">
            <div class="mini-square" style="background-color: #E3FCFF;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.act')">
            </div>
        </div>
        <div class="d-flex justify-content-start pt-3">
            <div class="mini-square" style="background-color: #FFE4B3;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.mente')">
            </div>
        </div>
        <div class="d-flex justify-content-start pt-3">
            <div class="mini-square" style="background-color: #FFC4B3;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.lugares')">
            </div>
        </div>
        <div class="d-flex justify-content-start pt-3">
            <div class="mini-square" style="background-color: #FF9797;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.palabras')">
            </div>
        </div>
        <div class="d-flex justify-content-start pt-3">
            <div class="mini-square" style="background-color: #fb5353;"></div>
            <div class="r-12-gray text-start ps-3" v-html="t('activity.hablar')">
            </div>
        </div>
    </div>
</template>

<script setup>

import { useI18n } from 'vue-i18n'
    import { computed } from 'vue'
  const { t } = useI18n();
  const { locale } = useI18n();
const weekDays = computed(() => {
  return locale.value === 'es'
    ? ['Lun', 'Mar', 'Mié', 'Jue', 'Vie', 'Sáb', 'Dom']
    : ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
});
import {
    Chart as ChartJS,
    BarElement,
    CategoryScale,
    LinearScale,
    Tooltip,
    Legend
} from 'chart.js'
import ChartDataLabels from 'chartjs-plugin-datalabels'
import { Bar } from 'vue-chartjs'

ChartJS.register(BarElement, CategoryScale, LinearScale, Tooltip, Legend, ChartDataLabels)

// Datos de gráfica de barras
const barData = computed(() => ({
  labels: weekDays.value,
  datasets: [
    {
      label: '',
      data: [15, 8, 2, 6, 10, 13, 5],
      backgroundColor: '#AAAAAA',
      barThickness: 14,
      borderRadius: 10,
      pointStyle: 'circle',
    }
  ]
}))

 
// const barData = {
//     labels: ['Lun', 'Mar', 'Mié', 'Jue', 'Vie', 'Sáb', 'Dom'],
//     datasets: [
//         {
//             label: '',
//             data: [15, 8, 2, 6, 10, 13, 5],
//             backgroundColor: '#AAAAAA',
//             barThickness: 14,
//             borderRadius: 10,
//             pointStyle: 'circle',
//         }
//     ]
// }

const barOptions = {
    layout: {
        padding: {
            top: 10
        }
    },
    responsive: true,
    plugins: {
        legend: {
            display: true,
            labels: {
                padding: 10,
                color: '#555',
                boxHeight: 0
            },
        },
        datalabels: {
            anchor: 'end',
            align: 'start',
            offset: -30,
            backgroundColor: '#000000',
            borderRadius: 999,
            color: '#ffffff',
            font: {
                weight: '500',
                size: 10,
            },
            padding: {
                top: 8,
                bottom: 8,
                left: 9,
                right: 9,
            },
            formatter: (value) => value,
        },
    },
    scales: {
        y: {
            beginAtZero: true,
            ticks: {
                display: false,
            },
            grid: {
                display: true
            },
        },
        x: {
            grid: {
                display: false,
            }
        },
    }
}

// Lista de colores definida en CSS
const colores = ["#efefef","#E3FCFF", "#FFE4B3", "#FFC4B3", "#FF9797", "#fb5353"];

// Generar un índice aleatorio
function obtenerColorAleatorio() {
    const indiceAleatorio = Math.floor(Math.random() * colores.length);
    return colores[indiceAleatorio];
}


// 📦 Datos fuente
// const data = {
//     'Lun': ['08:20', '12:30', '18:00', '19:00', '22:00', '23:00', '23:30'],
//     'Mar': ['10:30', '14:30', '18:30'],
//     'Mié': ['12:30'],
//     'Jue': ['10:00', '12:00', '13:00'],
//     'Vie': ['06:00', '16:00', '19:00', '21:00'],
//     'Sáb': ['09:00', '14:00', '19:00', '20:00', '22:00'],
//     'Dom': ['7:00', '14:00', '19:00']
// }
// const dataColor = {
//     'Lun': ['08:20', '12:30', '18:00', '19:00', '22:00', '23:00', '23:30'],
//     'Mar': ['10:30', '14:30', '18:30'],
//     'Mié': ['12:30'],
//     'Jue': ['10:00', '12:00', '13:00'],
//     'Vie': ['06:00', '16:00', '19:00', '21:00'],
//     'Sáb': ['09:00', '14:00', '19:00', '20:00', '22:00'],
//     'Dom': ['7:00', '14:00', '19:00']
// }
const dataColorEs = {
  'Lun': ['08:20', '12:30', '18:00', '19:00', '22:00', '23:00', '23:30'],
  'Mar': ['10:30', '14:30', '18:30'],
  'Mié': ['12:30'],
  'Jue': ['10:00', '12:00', '13:00'],
  'Vie': ['06:00', '16:00', '19:00', '21:00'],
  'Sáb': ['09:00', '14:00', '19:00', '20:00', '22:00'],
  'Dom': ['7:00', '14:00', '19:00']
}

const dayMap = {
  'Lun': 'Mon',
  'Mar': 'Tue',
  'Mié': 'Wed',
  'Jue': 'Thu',
  'Vie': 'Fri',
  'Sáb': 'Sat',
  'Dom': 'Sun'
}

// Columnas traducidas
const columnsColor = computed(() => {
  return locale.value === 'es'
    ? Object.keys(dataColorEs)
    : Object.keys(dataColorEs).map(dia => dayMap[dia])
})

// Datos traducidos
const dataColor = computed(() => {
  if (locale.value === 'es') return dataColorEs

  const translated = {}
  for (const [key, value] of Object.entries(dataColorEs)) {
    translated[dayMap[key]] = value
  }
  return translated
})


// 🔢 Obtener el máximo de filas que se deben mostrar
// const maxRows = Math.max(...columns.map(col => data[col].length))
// const maxRowsColor = Math.max(...columnsColor.map(col => dataColor[col].length))

// // 🔁 Función para acceder al valor en la "posición invertida"
// const getValueAt = (col, rowIndex) => {
//     return dataColor[col][rowIndex] ?? null
// }
const maxRowsColor = computed(() => {
  return Math.max(...columnsColor.value.map(col => dataColor.value[col]?.length || 0))
})

const getValueAt = (col, rowIndex) => {
  return dataColor.value[col]?.[rowIndex] ?? null
}
</script>

<style scoped>
table td,
table th {
    border: 1px solid #ccc;
}

.h6 {
    height: 24px;
    width: 32px;
}

/* .bg-red-300{
    background-color: red;
  } */
</style>